<template>
  <div>
    <div>
      <b-row>
        <b-col cols="12" xl="8">
          <b-card class="filterCard">
            <b-row>
              <b-col>
                <label>
                  {{ $t("FILTER") }}
                </label>
              </b-col>
            </b-row>
            <b-row>
              <b-col>
                <b-input-group
                  :prepend="$t('SUPPLIER_NAME')"
                  class="mb-2 mr-sm-2 mb-sm-0"
                >
                  <DropDownSearch
                    :searchList="supplierOptions"
                    :defaultItem="defaultDataItem"
                    @selected_person_data="getSelectedSupplier"
                    class="searchSelect1"
                  />
                </b-input-group>
              </b-col>
              <b-col>
                <b-input-group
                  :prepend="$t('RegisterdExpense')"
                  class="mb-2 mr-sm-2 mb-sm-0"
                >
                  <b-form-select
                    @change="onChange($event)"
                    v-model="selectedExpenseItem"
                    :options="expenseItemOptions"
                  ></b-form-select>
                </b-input-group>
              </b-col>
            </b-row>
          </b-card>
        </b-col>
        <b-col cols="12" xl="4">
          <div>
            <b-button
              :disabled="!getContentAccess()"
              @click="addSupplier"
              variant="primary"
              class="float-right mb-4"
              size="md"
              >{{ $t("ADDSUPPLIER") }}</b-button
            >
          </div>
        </b-col>
      </b-row>

      <b-modal
        id="supplier-modal"
        ref="supplier-modal"
        :hide-footer="true"
        centered
        variant="secondary"
        modal-class="py-0"
        body-class="py-0"
        size="md"
        footer-class="border-0"
        header-class="border-0 bg-white pd-0"
        button-size="sm"
        hide-header-close
      >
        <template v-slot:modal-header="{cancel}">
          <div class="w-100 d-flex justify-content-end">
            <b-button
              @click="cancel"
              class="back-btn close-btn mt-2"
              variant="primary"
              >{{ $t("CLOSE") }}</b-button
            >
          </div>
        </template>
        <b-card class="pop-up-box">
          <b-row>
            <b-col>
              <div class="font-weight-bold">{{ $t("SUPPLIERSELECT") }}</div>
              <b-form-group>
                <b-form-radio-group
                  id="radio-group-1"
                  v-model="supplierSelect"
                  :options="supplierOptions"
                  class="radioControl mb-3 pt-2"
                  size="md"
                  stacked
                ></b-form-radio-group>
              </b-form-group>
            </b-col>
          </b-row>
        </b-card>
        <b-row>
          <b-col cols="12" class="text-center mb-2">
            <b-button @click="getSupplier" variant="primary" class="float-right"
              >{{ $t("ADD") }}
              <!-- <i v-if="selectLoader" class="fa fa-spinner fa-spin" /> -->
            </b-button>
          </b-col>
        </b-row>
      </b-modal>

      <!-- <b-card> -->
      <b-row class="table-height">
        <b-col xl="12">
          <b-table
            ref="selectableTable"
            :fields="fields"
            :items="tableData"
            :per-page="limit"
            :total="total"
            :startPage="start"
            @change-page="pageChange"
            :busy="isBusy"
            :bordered="false"
            :striped="false"
            :borderless="false"
            :outlined="false"
            head-variant="light"
            small="small"
            fixed
            sticky-header
            show-empty
          >
            <template v-slot:cell(checkState)="item">
              <b-form-radio
                :id="'radio-' + item.index"
                v-model="item.item.checkState"
                :name="'table-radio'"
                @change="toggleSelectAll(item)"
              ></b-form-radio>              
            </template>
            <template v-slot:cell(supplier)="item">{{ item.value }}</template>
            <template v-slot:cell(address)="item">{{ item.value }}</template>
            <template v-slot:cell(registerdExpense)="item">{{
              item.value
            }}</template>
            <template v-slot:cell(personIncharge)="item">{{
              item.value
            }}</template>
            <template v-slot:cell(createDate)="item">{{ item.value }}</template>
            <template v-slot:cell(updateDate)="item">{{ item.value }}</template>
            <template v-slot:cell(icon)="item">
              <b-button
                :disabled="!getContentAccess()"
                @click="editSupplier(item)"
                variant="primary"
                size="md"
                class="editBtn"
                >{{ $t("MASTERMAINTENANCE.EDIT") }}</b-button
              >
            </template>
            <template v-slot:empty>
              <div class="text-center errorColor">
                {{ $t("GENRALDATAERRORMESSAGE") }}
              </div>
            </template>
            <template v-slot:table-busy>
              <div class="text-center text-danger my-2">
                <b-spinner class="align-middle"></b-spinner>
              </div>
            </template>
          </b-table>
        </b-col>
      </b-row>
      <!-- </b-card> -->
    </div>
    <b-row>
      <b-col cols="12" xl="4">
        <b-button
          @click="deleteSelectedSupplier"
          :disabled="
            checkedItems.length == 0 ||
              checkedItems.length > 1 ||
              !getContentAccess()
          "
          variant="primary"
          >{{ $t("DELETEBATCHSUPPLIER")
          }}<i v-if="loading" class="fa fa-spinner fa-spin"
        /></b-button>
      </b-col>
      <b-col cols="12" xl="5">
        <b-pagination
          v-model="currentPage"
          :total-rows="totalRows"
          :per-page="limit"
          class="paginateProcess"
          align="center"
          size="sm"
        >
          <template v-slot:prev-text>{{ $t("PREVIOUS") }}</template>
          <template v-slot:next-text>{{ $t("NEXT") }}</template>
          <template v-slot:page="{page, active}">
            <strong v-if="active">{{ page }}</strong>
            <i v-else>{{ page }}</i>
          </template>
        </b-pagination>
      </b-col>
      <b-col cols="12" xl="3">
        <div class="countList">
          <label>{{ $t("MEANS") }}</label
          >&nbsp;&nbsp;
          <template v-for="(page, index) in pageOptions">
            <b-link :key="index" v-if="limit == page" href="javascript:;">
              {{ page }}
              <span v-if="index != pageOptions.length - 1">&nbsp;|&nbsp;</span>
            </b-link>
            <b-link
              :key="index"
              @click="perPageChange(page)"
              v-else
              href="javascript:;"
            >
              <u>{{ page }}</u>
              <span v-if="index != pageOptions.length - 1">&nbsp;|&nbsp;</span>
            </b-link>
          </template>
        </div>
      </b-col> </b-row
    ><br />
  </div>
</template>

<script>
import {ApiServicesFactory} from "@/shared/apiServiceFactory";
const BasicInfoApis = ApiServicesFactory.get("basic");
import DropDownSearch from "@/components/DropDownSearch.vue";
import {endPoint} from "@/shared/endPoint";

export default {
  name: "SupplierList",
  components: {
    DropDownSearch,
  },
  data() {
    return {
      loading: false,
      supplierSelect: "",
      selectedSupplier: {
        supplier_no: "",
        supplier_name: "",
      },
      selectedExpenseItem: {
        accounts_name_cd: "",
        expense_item_cd: "",
      },
      supplierOptions: [
        {value: "1", text: "サプライヤーA"},
        {value: "2", text: "サプライヤーB"},
        {value: "3", text: "サプライヤーC"},
      ],
      expenseItemOptions: [
        {
          value: {accounts_name_cd: "0001", expense_item_cd: "001"},
          text: "AAA",
        },
        {
          value: {accounts_name_cd: "0002", expense_item_cd: "002"},
          text: "BBB",
        },
        {
          value: {accounts_name_cd: "0003", expense_item_cd: "003"},
          text: "CCC",
        },
      ],
      defaultDataItem: {
        item: "",
        value: "",
      },
      total: 0,
      limit: 50,
      start: 0,
      currentPage: 1,
      pageOptions: [50, 100, 200],
      checkedItems: [],
      tableData: [],
      isBusy: false,
    };
  },
  computed: {
    totalRows() {
      if (this.total) {
        return this.total;
      }
      return this.getRowCount();
    },
    items() {
      return this.tableData;
    },
    fields() {
      return [
        {
          key: "checkState",
          label: "",
          class: "text-center button-column-width",
        },
        {
          key: "supplier",
          label: this.$t("EXPENSEREDUCTION.Supplier"),
          class: "text-left title-normal-width",
        },
        {
          key: "address",
          label: this.$t("Place"),
          class: "text-left title-normal-width",
        },
        {
          key: "registerdExpense",
          label: this.$t("RegisterdExpense"),
          class: "text-left normal-width",
        },
        {
          key: "personIncharge",
          label: this.$t("SUPPLIERPERSONINCHARGE"),
          class: "text-left title-normal-width",
        },
        {
          key: "createDate",
          label: this.$t("CreateDate"),
          class: "text-center date-normal-width",
        },
        {
          key: "updateDate",
          label: this.$t("UpdateDate"),
          class: "text-center date-normal-width",
        },

        {
          key: "icon",
          label: "",
          class: "text-left button-column-width",
        },
      ];
    },
  },
  watch: {
    currentPage() {
      this.pageChange(this.currentPage);
    },
    startPage() {
      this.currentPage = this.startPage + 1;
    },
  },
  async mounted() {
    await this.getSupplierData();
    this.getSupplierList();
    this.getExpenseItemList();
  },
  methods: {
    /**
     * @description function to navigate to the supplier add/edit screen
     * @name addSupplier
     * @param none
     * @returns nothing
     */
    addSupplier() {
      this.$store.dispatch("updateSupplierData", {
        id: null,
      });
      this.$router.push({
        name: "supplier_addEdit",
      });
    },
    /**
     * @description function to show supplier modal popup
     * @name showSupplierSelect
     * @param none
     * @returns nothing
     */
    showSupplierSelect() {
      this.supplierSelect = "";
      this.$refs["supplier-modal"].show();
    },
    /**
     * @description function to get the supplier and hide the modal popup
     * @name getSupplier
     * @param none
     * @returns noting
     */
    getSupplier() {
      this.$refs["supplier-modal"].hide();
    },
    /**
     * @description function to get the count of the items
     * @name getRowCount
     * @param none
     * @returns nothing
     */
    getRowCount() {
      return this.items.length;
    },
    /**
     * @description function trigger when the page change button clicked
     * @name pageChange
     * @param page
     * @returns nothing
     */
    pageChange(page) {
      this.start = page - 1;
      this.getSupplierData();
    },
    /**
     * @description function trigger when the page change button clicked
     * @name perPageChange
     * @param page
     * @returns nothing
     */
    perPageChange(page) {
      this.limit = page;
      this.getSupplierData();
    },
    /**
     * @description function to get the supplier when dropdown value changes
     * @name onChange
     * @param none
     * @returns nothing
     */
    onChange() {
      this.getSupplierData();
    },
    /**
     * @description function to get access content from localstorage
     * @name getContentAccess
     * @param none
     * @returns boolean
     */
    getContentAccess() {
      let person_auth = localStorage.getItem("person_auth");
      return person_auth == "02" ? true : false;
    },
    /**
     * @description function to delete the selected supplier
     * @name deleteSelectedSupplier
     * @param none
     * @returns nothing
     */
    async deleteSelectedSupplier() {
      this.$swal({
        text: this.$t("AREYOUSUREDELETESUPPLIER"),
        type: "warning",
        showCancelButton: true,
        confirmButtonText: this.$t("YESDELETE"),
        cancelButtonText: this.$t("NODELETE"),
      }).then(async result => {
        if (result.value) {
          this.loading = true;
          let selectedItems = this.checkedItems;
          const {data} = await BasicInfoApis.postAPI(
            endPoint.SUPPLIERSOFTDELETE,
            {
              supplier_no: selectedItems[0].supplier_no,
              update_id: localStorage.getItem("personID"),
            }
          );

          if (data.success) {
            this.$swal({type: "success", text: this.$t("DELETED")});
            this.loading = false;
            this.getSupplierData();
          } else {
            this.$swal({type: "error", text: this.$t("NOTDELETED")});
            this.loading = false;
          }
        }
      });
    },
    /**
     * @description function to get the expense item list
     * @name getExpenseItemList
     * @param none
     * @returns nothing
     */
    async getExpenseItemList() {
      const {data} = await BasicInfoApis.postAPI(endPoint.READMEXPENSEITEM, {
        fieldlist: ["accounts_name_cd", "expense_item_cd", "expense_item_nm"],
        conditions: [
          {
            key: "del_flg",
            value: false,
          },
        ],
      });
      if (data.success) {
        let responseData = data.data;
        let expenseListData = [];
        if (responseData.length) {
          responseData.forEach(item => {
            let iterateExpenseItem = {
              text: item.expense_item_nm ? item.expense_item_nm : "",
              value: {
                accounts_name_cd: item.accounts_name_cd
                  ? item.accounts_name_cd
                  : "",
                expense_item_cd: item.expense_item_cd
                  ? item.expense_item_cd
                  : "",
              },
            };
            expenseListData.push(iterateExpenseItem);
          });
          this.expenseItemOptions = expenseListData;
        } else {
          this.expenseItemOptions = [];
        }
      } else {
        this.expenseItemOptions = [];
      }
    },
    /**
     * @description function to get the suppliers
     * @name getSupplierList
     * @param none
     * @returns nothing
     */
    async getSupplierList() {
      const {data} = await BasicInfoApis.postAPI(
        endPoint.MASTERREADDATAMSUPPLIER,
        {
          fieldlist: ["supplier_no", "supplier_name"],
          conditions: [
            {
              key: "del_flg",
              value: false,
            },
          ],
        }
      );
      if (data.success) {
        let flag = this.getSupplierFilter();
        let responseData;
        let filterData = [];
        if (flag) {
          if (this.tableData.length > 0) {
            this.tableData.forEach(item => {
              let data = {
                supplier_name: item.supplier,
                supplier_no: item.supplier_no,
              };
              filterData.push(data);
            });
            responseData = filterData;
          } else {
            responseData = data.data;
          }
        } else {
          responseData = data.data;
        }
        let listData = [];
        if (responseData.length) {
          responseData.forEach(item => {
            let iterateSupplier = {
              text: item.supplier_name ? item.supplier_name : "",
              value: item.supplier_no ? item.supplier_no : "",
            };
            listData.push(iterateSupplier);
          });
          this.supplierOptions = listData;
        } else {
          this.supplierOptions = [];
        }
      } else {
        this.supplierOptions = [];
      }
    },
    /**
     * @description fucntion to get the selected supplier
     * @name getSelectedSupplier
     * @param item
     * @returns nothing
     */
    getSelectedSupplier(item) {
      this.selectedSupplier = {
        supplier_no: item.value ? item.value : "",
        supplier_name: item.text ? item.text : "",
      };
      this.getSupplierData();
    },
    /**
     * @description function to get the supplier auth from the localstorage
     * @name getSupplierFilter
     * @param none
     * @returns string
     */
    getSupplierFilter() {
      let supplier_auth = localStorage.getItem("supplier_auth");
      return supplier_auth;
    },
    /**
     * @description function to get the supplier data
     * @name getSupplierData
     * @param none
     * @returns nothing
     */
    async getSupplierData() {
      this.checkedItems = [];
      this.isBusy = true;
      const {data} = await BasicInfoApis.postAPI(endPoint.SUPPLIERLIST, {
        supplier_name: this.selectedSupplier.supplier_name,
        supplier_no: this.selectedSupplier.supplier_no,
        expense_item_cd: this.selectedExpenseItem.expense_item_cd,
        accounts_name_cd: this.selectedExpenseItem.accounts_name_cd,
        person_id: localStorage.getItem("personID"),
        limit: this.limit,
        offset: this.start * this.limit,
        paginate: true,
      });
      this.total = 0;
      this.isBusy = false;
      if (data.success) {
        let responseData = data.data.supplierList;
        this.total = data.data.total;
        let rowData = [];

        responseData.forEach(item => {
          let mapData = {
            checkState: false,
            supplier: item.supplier_name ? item.supplier_name : "",
            supplier_no: item.supplier_no ? item.supplier_no : "",
            accounts_name_cd: item.accounts_name_cd
              ? item.accounts_name_cd
              : "",
            expense_item_cd: item.expense_item_cd ? item.expense_item_cd : "",
            address: item.supplier_address ? item.supplier_address : "",
            registerdExpense: item.expense_item_nm ? item.expense_item_nm : "",
            personID: item.person_id ? item.person_id : "",
            personIncharge: item.person_name ? item.person_name : "",
            createDate: item.create_dt ? this.getDate(item.create_dt) : "",
            updateDate: item.update_dt ? this.getDate(item.update_dt) : "",
          };
          rowData.push(mapData);
        });
        this.tableData = rowData;
      } else {
        this.tableData = [];
      }
    },
    /**
     * @description function to get the formatted date
     * @name getDate
     * @param date
     * @returns nothing
     */
    getDate(date) {
      if (!date) {
        return "";
      }
      return (
        date.slice(0, 4) + "/" + date.slice(5, 7) + "/" + date.slice(8, 10)
      );
    },
    /**
     * @description function to dispay the checkbox control
     * @name getCheckBoxControl
     * @param none
     * @returns nothing
     */
    getCheckBoxControl() {
      return true;
    },
    /**
     * @description function to toggle the select all checkboxes
     * @name toggleSelectAll
     * @param item
     * @returns nothing
     */
    toggleSelectAll(item) {
      this.checkedItems = [];
      let selectedRow = item.item;
      this.checkedItems.push(selectedRow);
    },
    /**
     * @description function to navigate add/edit supplier screen
     * @name editSupplier
     * @param item
     * @returns nothing
     */
    editSupplier(item) {
      this.$store.dispatch("updateSupplierData", {
        id: item.item.supplier_no,
      });
      this.$router.push({name: "supplier_addEdit"});
    },
  },
};
</script>
<style scoped lang="scss">
.searchSelect1 {
  position: relative !important;
  flex: 1 1 auto !important;
  width: 1% !important;
  margin-bottom: 0 !important;
  border: 1px solid grey !important;
  border-top-left-radius: 0 !important;
  border-bottom-left-radius: 0 !important;
  height: 30px;
}
.editBtn {
  min-width: 75px !important;
}
.normal-width {
  width: 80px !important;
}
.button-column-width {
  width: 80px !important;
}
.title-normal-width {
  width: 110px !important;
}
.date-normal-width {
  width: 110px !important;
}
.check-normal-width {
  width: 80px !important;
  //padding-top: 15px !important;
}
.table th,
.table td {
  padding: 0.5rem !important;
}
.form-control {
  border-radius: inherit;
  height: 30px;
}
.countList {
  float: right;
  padding-top: 10px;
}
.radioControl {
  padding-bottom: 5px !important;
}

.custom-select {
  height: 30px;
  border-color: rgb(163, 163, 163);
}
.paginateProcess {
  padding-top: 10px;
}
.input-group-text {
  height: 30px !important;
  width: 120px;
  font-weight: 700;
  background-color: #ffff;
  border-color: rgb(163, 163, 163);
  border: 2px solid rgb(163, 163, 163);
}
.filterCard {
  border: 1px dashed;
}
#supplier-modal .card-body {
  max-height: 500px;
  overflow: auto;
}
</style>
<style lang="scss">
.button-column-width {
  width: 80px !important;
}
</style>
